export default {
	onLoad() {
		uni.getStorage({
			key: "userinfo",
			complete: (res) => {
				if (res.data == null || res.data == '') {
					this.hasPwd = false
				} else {
					this.hasPwd = true
					this.userInfo = JSON.parse(res.data)
				}
			}
		})
	},
	data() {
		return {
			hasPwd: false,
			userInfo: {},
			btnIsDisable: true,
			oldPwd: "",
			newPwd: "",
			newConfirmPwd: "",
			loading: false
		}
	},
	watch: {
		oldPwd(val) {
			if (this.inputIsEmpty()) {
				this.btnIsDisable = true
			} else {
				this.btnIsDisable = false
			}
		},
		newPwd(val) {
			if (this.inputIsEmpty()) {
				this.btnIsDisable = true
			} else {
				this.btnIsDisable = false
			}
		},
		newConfirmPwd(val) {
			if (this.inputIsEmpty()) {
				this.btnIsDisable = true
			} else {
				this.btnIsDisable = false
			}
		}
	},
	methods: {
		handleConfirm() {
			if (!this.inputIsEmpty() && this.newPwd == this.newConfirmPwd) {
				this.loading = true
				this.btnIsDisable = true
				this.request({
					url: this.config.BASE_URL + "repassword",
					method: "post",
					data: {
						oldpassword: this.hasPwd ? this.oldPwd : 0,
						newpassword: this.newPwd,
						renewpassword: this.newConfirmPwd
					},
					header: {
						token: this.userInfo.token
					}
				}).then(res => {
						console.log(res)
						uni.showToast({
							title: res.msg,
							success() {
								if (res.errorCode || res.errorCode != null) {

								} else {
									uni.navigateBack({
										delta: 1
									})
								}
							}
						})
				})
		} else {
			uni.showToast({
				title: "两次新密码不一致",
				icon: "none"
			})
		}
	},
	inputIsEmpty() {

		if (this.oldPwd && this.oldPwd != null && this.newPwd && this.newPwd != null && this.newConfirmPwd && this.newConfirmPwd !=
			null) {
			return false;
		}
		return true;
	}
}
}
